.filters{display:flex;flex-direction:column}.filters .wrapper .header{text-align:center;text-transform:uppercase;color:#494949}.filters .wrapper ul{padding:0;list-style-type:none;display:flex;flex-wrap:wrap;justify-content:space-around}.filters .wrapper ul li{text-align:center;text-transform:uppercase;height:60px;width:120px;margin-top:10px;color:#20bf6b;padding:10px;border:4px solid #20bf6b;border-radius:6px;display:inline-block;transition:all 0.3s ease 0s;cursor:pointer}.filters .wrapper ul li:hover{color:#494949;border-radius:50px;border-color:#494949;transition:all 0.3s ease 0s}.filters .wrapper ul li.active{color:brown;border-color:brown}@media screen and (max-width: 1080px){.filters .wrapper .header{font-size:30px}.filters .wrapper ul li{width:320px;height:80px;font-size:25px}}@media screen and (max-width: 840px){.filters .wrapper .header{font-size:60px}.filters .wrapper ul li{width:400px;height:100px;font-size:40px}}
