.news-block{display:flex;flex-direction:column;align-items:center}.news-block .news{display:flex;flex-direction:column;margin-top:20px;padding:20px;width:1024px;border:4px solid #20bf6b}.news-block .news img{height:700px;margin-top:20px}.news-block .news .url{text-align:center;text-transform:uppercase;margin-top:10px;color:#494949}.news-block .news div{text-align:center;text-transform:uppercase;margin-top:20px;color:#494949}.news-block .news:hover{border-radius:50px;border-color:#494949;transition:all 0.3s ease 0s}@media screen and (max-width: 1080px){.news-block .news{width:768px}.news-block .news img{height:500px}}@media screen and (max-width: 840px){.news-block .news{width:450px}.news-block .news img{height:300px}}
